/**
 * @description 验证手机号格式
 * @param {string} value 
 * @returns true -- 格式正确
 *          false -- 格式错误
 *          null -- 没有输入
 */
 export function checkPhone(value) {
  var regex = /^1[3458]\d{9}$/; 
  if (value) {
      if (regex.test(value)) { 
          return true;
      } else { 
          return false;
      }
  } else {
      return null;
  }
}

/**
 * 验证邮箱格式
 * @param {string} email 
 * @returns true -- 格式正确
 *          false -- 格式错误
 *          null -- 没有输入
 */
export function checkEmail(email) {
  var reg = RegExp('[a-zA-Z0-9]+@[a-zA-Z0-9]+\\.[a-zA-Z0-9]+')
  if (email) {
    if(reg.test(email)) {
      return true;
    } else {
      return false;
    }
  } else {
    return null;
  }
}