import axios from "axios"
import { BASE_URL, TIMEOUT, CONTENT_TYPE, URL} from '../misc/config'

const Axios = axios.create({
  baseURL: BASE_URL,
  timeout: TIMEOUT,
  headers: {'Content-Type': CONTENT_TYPE},
  withCredentials: true,
});

export const API = {

  SignIn : function (data) {return Axios.post(URL.login, data);},

  getCaptcha: function (phone) {
    return Axios.get(URL.getcap, {
      params: phone
    })
  },

  SignUp : function (data) {return Axios.post(URL.signup, data);},

  getRank : function(status) {
    return Axios.get(URL.rank, {
      params: status // 注意status是object {gameStatus: xxx}
    })
  },

  getCurrent : function() {return Axios.get(URL.myinfo)},

  LogOut : function() {return Axios.get(URL.logout)}
}


// a response interceptor
Axios.interceptors.response.use(function (response) {
  switch(response.data.code){
      case "-1":
          console.log("code: -1")
          if(response.data.data === null && response.data.message === "failed") {
            throw new Error("用户名或密码不正确");
          }
          break;
      case "102":
          console.log("code: 102");
          if(response.data.data === "Empty username" && response.data.message === "value invalid"){
              throw new Error("用户名不能为空！");
          }else if(response.data.data === "Empty password" && response.data.message === "value invalid"){
              throw new Error("密码不能为空！");
          }
          break;
      case "997":
          console.log("code: 997");
          if(response.data.message === "apply captcha too frequently" && response.data.data === null){
              throw new Error("获取验证码太频繁！");
          }
          break;
      case "104":
          console.log("code: 104");
          if(response.data.message === "need login"){
            throw new Error("未登陆！");
          }
          break;
      case "105":
          console.log("code: 105");
          if(response.data.message === "verification code error" && response.data.data === null){
            throw new Error("验证码错误！");
          }
          break;
      case "108":
        console.log("code: 108");
        if(response.data.message === "apply captcha too frequently"){
          throw new Error("获取验证码太频繁！");
        }
        break;
      default:
          break;
  }
  return response;
}, function (error) {
  return Promise.reject(error);
  // return error;
});


// a request interceptor
Axios.interceptors.request.use(function (config) {
  // Do something before request is sent
  return config;
}, function (error) {
  // Do something with request error
  return Promise.reject(error);
});

export default Axios;